.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.gallery-container {
  overflow: hidden;
}

.mosaic {
  transition: transform 0.5s ease-out;
}

.gallery-image {
  max-width: 100%;
  max-height: 100vh; /* Adjust based on your layout */
  object-fit: cover;
}

.zoomed {
  transform: scale(1.5); /* Adjust based on desired zoom level */
  transform-origin: center; /* Adjust if you want to zoom in on a specific part of the mosaic */
}

@import url('https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,400..800;1,400..800&family=Hedvig+Letters+Serif:opsz@12..24&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Noto+Sans:ital,wght@0,100..900;1,100..900&display=swap');

body {
  min-height: 100vh; /* Ensure the body covers at least viewport height */
  position: relative; /* Necessary for absolute positioning within the body */
  margin: 0; /* Reset browser's default margins */
  background-color: #edcfb04e;
  font-family: "Noto Sans", sans-serif;
}

.screen {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content:right;
  /* align-items: center; */
  flex-direction: row;
  padding: 15vh 0;
}

.screen.visible img, 
.screen.visible .text-box {
    opacity: 1;
}

.image-block {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  height: auto;
  margin: 2px;
}

.image-block img {
  object-fit: cover;  /* Or 'contain' if you prefer */
  min-width: 150px;  /* Optional: Set a minimum image width */
  margin: 2px; 
}

.text-box {
  padding: 10px;
}

.content-wrapper {
  display: flex; 
  flex-wrap: wrap;
  justify-content: space-around; 
  min-width: 50%;
}

.footer a {
  display: inline-block; /* Adjust as needed */
  padding: 10px 20px;
  background-color: #333; /* Example background color */
  color: white;
  text-decoration: none;
  border-radius: 5px; /* Add a rounded edge for a button-like look */
}